html,
body {
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

#evernote-container {
  height: 700vh;
  box-sizing: border-box;
}

.app-container {
  height: 100%;
  box-sizing: border-box;
}

.quill {
  height: 100%;
  box-sizing: border-box;
  width: 70vw;
  float: left;
}
.ql-container {
  box-sizing: border-box;
  height: calc(100%-42px);
  overflow-y: scroll;
}

.ql-editor {
  cursor: text;
}

.MuiListItem-root.Mui-selected {
  background-color: #d4d8e8 !important;
}

.no-note-selected {
  font-size: 48px;
  position: absolute;
  top: 50px;
  left: calc(50%-165px);
}
